:root {
  --main-bg-color: #0a192f;
  --white: #dce2f0;
  --green: #64ffda;
  --slate: #8892b0;
  --light-slate: #a8b2d1;
  --light-navy: #112240;
  --lightest-navy: #233554;
  --fz-xs: 13px;
  --fz-md: 16px;
  --fz-xl: 20px;
  --fz-heading: 32px;
  --tab-height: 42px;
  --border-radius: 4px;
  --font-sans: "Noto Sans KR", sans-serif;
  --font-mono: "DM Mono", monospace;
}

body {
  margin: 0;
  color: var(--white);
  background-color: var(--main-bg-color);
  font-family: var(--font-sans);
}

#root {
  height: 100vh;
}

header {
  position: fixed;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 0 50px;
}

main {
  width: 100%;
  padding: 0 150px;
  box-sizing: border-box;
  counter-reset: section 0;
}

@media (max-width: 1080px) {
  main {
    padding: 0px 100px;
  }
}

@media (max-width: 830px) {
  main {
    padding: 0px 50px;
  }
}

@media (max-width: 480px) {
  header,
  main {
    padding: 0px 25px;
  }
}

section {
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  flex-direction: column;
  max-width: 1000px;
  height: 100vh;
  margin: 0 auto;
}

.numbered-heading {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  margin: 10px 0px 40px;
  width: 100%;
  font-size: clamp(26px, 5vw, var(--fz-heading));
  white-space: nowrap;
}

.numbered-heading::before {
  position: relative;
  bottom: 4px;
  counter-increment: section 1;
  content: "0" counter(section) ".";
  margin-right: 10px;
  color: var(--green);
  font-family: var(--font-mono);
  font-size: clamp(var(--fz-md), 3vw, var(--fz-xl));
  font-weight: 400;
}

.numbered-heading::after {
  content: "";
  width: 300px;
  height: 1px;
  margin-left: 20px;
  background-color: var(--lightest-navy);
}

.project-heading {
  margin-top: 0;
}

p {
  margin: 0 0 15px;
  color: var(--slate);
}

.strong-paragraph {
  color: var(--green);
  margin: 0;
}

.project-overline {
  font-size: var(--fz-xs);
}

.project-desc {
  box-shadow: 0 10px 30px -15px var(--navy-shadow);
  border-radius: var(--border-radius);
  color: var(--light-slate);
}

@media (min-width: 830px) {
  .project-desc {
    background-color: var(--light-navy);
    padding: 25px;
  }
}

@media (max-width: 580px) {
  .project-desc {
    display: none;
  }
}

.project-img {
  filter: brightness(30%);
}

.project-img:hover {
  filter: brightness(90%);
  transition-duration: 0.7s;
}

ul {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

a {
  text-decoration: none;
  color: var(--white);
}

button {
  cursor: pointer;
}

.green-btn {
  color: var(--green);
  background-color: transparent;
  border: 1px solid var(--green);
  border-radius: var(--border-radius);
  padding: 1.25rem 1.75rem;
  font-size: var(--fz-sm);
  text-decoration: none;
  cursor: pointer;
  margin-top: 40px;
}

.fade-in {
  animation: fadein 0.5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
